.info {
  &.info-hover {
    .info-title {
      transition: color 0.4s;
    }

    &:hover {
      .icon {
        @include transform-translate-y(-0.5rem);

        &.icon-primary {
          &.icon-circle {
            box-shadow: 0px 15px 30px 0px rgba(249, 99, 50, 0.3);
          }
        }
        &.icon-secondary {
          &.icon-circle {
            box-shadow: 0px 15px 30px 0px $secondary-color-opacity;
          }
        }
        &.icon-info {
          &.icon-circle {
            box-shadow: 0px 15px 35px 0px rgba(44, 168, 255, 0.3);
          }
        }
        &.icon-success {
          &.icon-circle {
            box-shadow: 0px 15px 35px 0px rgba(24, 206, 15, 0.3);
          }
        }
        &.icon-warning {
          &.icon-circle {
            box-shadow: 0px 15px 35px 0px rgba(255, 178, 54, 0.3);
          }
        }
        &.icon-danger {
          &.icon-circle {
            box-shadow: 0px 15px 35px 0px rgba(255, 54, 54, 0.3);
          }
        }

        &.icon-info + .info-title {
          color: $info-color;
        }

        &.icon-warning + .info-title {
          color: $warning-color;
        }

        &.icon-danger + .info-title {
          color: $danger-color;
        }

        &.icon-primary + .info-title {
          color: $primary-color;
        }

        &.icon-success + .info-title {
          color: $success-color;
        }
      }
    }
  }

  .icon {
    color: $default-color;
    transition: transform 0.4s, box-shadow 0.4s;

    > i {
      font-size: 2.3em;
    }

    &.icon-circle {
      max-width: 70px;
      width: 70px;
      height: 70px;
      margin: 0 auto;
      border-radius: 50%;
      box-shadow: 0px 9px 35px -6px rgba(0, 0, 0, 0.3);
      font-size: $font-size-mini;
      background-color: $white-color;
      position: relative;

      i {
        line-height: 2.6em;
      }
    }
  }

  .info-title {
    margin: 13px 0 10px;
    padding: 0 15px;
    color: $secondary-color;
    font-weight: $font-weight-normal;
    font-size: rem(30px);
    line-height: rem(36px)
  }
  p {
    color: $default-color;
    padding: 0 15px;
    font-size: 1.1em;
  }
}

.info-horizontal {
  text-align: left !important;

  .icon {
    float: left;
    margin-top: 23px;
    margin-right: 10px;

    > i {
      font-size: 2em;
    }

    &.icon-circle {
      width: 65px;
      height: 65px;
      max-width: 65px;
      margin-top: 8px;

      i {
        display: table;
        margin: 0 auto;
        line-height: 3.5;
        font-size: 1.9em;
      }
    }
  }
  .description {
    overflow: hidden;
  }
}

.icon {
  &.icon-primary {
    color: $brand-primary;

    &.icon-circle {
      box-shadow: 0px 9px 30px -6px rgba($primary-color, 0.5);
    }
  }
  &.icon-secondary {
    color: $brand-secondary;

    &.icon-circle {
      box-shadow: 0px 9px 30px -6px rgba($secondary-color, 0.5);
    }
  }
  &.icon-info {
    color: $brand-info;

    &.icon-circle {
      box-shadow: 0px 9px 30px -6px rgba($info-color, 0.5);
    }
  }
  &.icon-success {
    color: $brand-success;

    &.icon-circle {
      box-shadow: 0px 9px 30px -6px rgba($success-color, 0.5);
    }
  }
  &.icon-warning {
    color: $brand-warning;

    &.icon-circle {
      box-shadow: 0px 9px 30px -6px rgba($warning-color, 0.5);
    }
  }
  &.icon-danger {
    color: $brand-danger;

    &.icon-circle {
      box-shadow: 0px 9px 30px -6px rgba($danger-color, 0.5);
    }
  }

  &.icon-white {
    color: $white-color;
  }
}
