.card {
  border: 0;
  border-radius: $border-radius-small;
  display: inline-block;
  position: relative;
  width: 100%;
  margin-bottom: 30px;
  box-shadow: $box-shadow;

  [data-notify="container"].alert {
    min-width: auto;
    left: unset !important;
    right: unset !important;
  }

  .card-body {
    padding: 15px 15px 10px 15px;

    &.table-full-width {
      padding-left: 0;
      padding-right: 0;
    }
  }

  .card-header {
    &:not([data-background-color]) {
      background-color: transparent;
    }
    padding: 15px 15px 0;
    border: 0;

    .card-title {
      margin-top: 10px;
    }
  }

  .map {
    border-radius: $border-radius-small;

    &.map-big {
      height: 400px;
    }
  }

  &[data-background-color="orange"] {
    background-color: $primary-color;

    .card-header {
      background-color: $primary-color;
    }

    .card-footer {
      .stats {
        color: $white-color;
      }
    }
  }

  &[data-background-color="red"] {
    background-color: $danger-color;
  }

  &[data-background-color="yellow"] {
    background-color: $warning-color;
  }

  &[data-background-color="blue"] {
    background-color: $info-color;
  }

  &[data-background-color="green"] {
    background-color: $success-color;
  }

  .image {
    overflow: hidden;
    height: 200px;
    position: relative;
  }

  .avatar {
    width: 30px;
    height: 30px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 15px;
  }

  label {
    font-size: $font-size-base;
    margin-bottom: 5px;
  }

  .card-footer {
    background-color: transparent;
    border: 0;

    .stats {
      i {
        margin-right: 5px;
        position: relative;
        top: 2px;
      }
    }

    .btn {
      margin: 0;
    }
  }
  &.card-service-details {
    min-height: 335px;
  }

  &.card-login {
    height: 385px;
    .card-header {
      margin-bottom: 63px;
      .logo-container {
        margin-bottom: 0;
      }
    }
    .card-body {
      .verification-error,
      .passcode-error {
        margin-top: 7px;
        text-align: center;
        color: red;
      }
    }
    
    #loginFooter {
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      .btn-link {
        position: absolute;
        bottom: 63px;
        left: 50%;
        letter-spacing: 0.6px;
        transform: translateX(-50%);
        width: 100%;
        font-weight: 600;
        font-size: 17px;
        padding: 0;
      }
      .spinner {
        color: #865BF4;
        font-size: 16px;
        position: absolute;
        bottom: 63px;
      }
    }
  }
}
