    @include social-buttons-color(
      $social-facebook,
      $social-facebook-state-color
    );
  }
  &.btn-twitter {
    @include social-buttons-color($social-twitter, $social-twitter-state-color);
  }
  &.btn-pinterest {
    @include social-buttons-color(
      $social-pinterest,
